import apiRegister from '@/api/register'
/**
  @desc 
  @method
    createRegister   
 */
const actions = {
  createRegister ({ commit }, registerUser) {
    return new Promise((resolve, reject) => {
      apiRegister.createRegister(registerUser)
        .then(res => {
          resolve(res)
        })
        .catch(err => {
          reject(err)
        })
    })
  }
}

export default {
  actions
}
