import router from '@/router'
import store from '@/store'
import axios from 'axios'
import { getToken, removeToken } from '@/utils/auth'
// import { startLoading, endLoading } from './loading'
import Swal from 'sweetalert2'
/**
  @desc global axios  API
     jwt  header  server 
    request ->  jwt  header  jwt  server
    response -> server  state  Vue  jwtToken
     header  token 
*/
axios.defaults.baseURL = process.env.VUE_APP_API_HOST
axios.defaults.headers.common.Accept = 'application/json, */*'
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'
// axios.defaults.timeout = 50000

// Add a request interceptor  Vuex  jwt
axios.interceptors.request.use(
  (config) => {
    // startLoading()
    if (getToken()) {
      config.headers.Authorization = `Bearer ${getToken()}`
    }
    return config
  },
  (error) => {
    return Promise.reject(error)
  })

/**
  @desc  Add a response interceptor  Vue 
  @param error {object}  server 
*/
axios.interceptors.response.use(
  (response) => {
    // endLoading()
    return response
  },
  (error) => {
    // endLoading()
    const { response } = error

    // jwt 
    if (response.status === 401 && response.data === 'Unauthorized') {
      removeToken('TokenKey')
      store.getters.jwtToken = ''
      Swal.fire({
        icon: 'error',
        title: ''
      })
      router.push('/')
    }
    if (response.status !== 200) {
      Swal.fire({
        icon: 'error',
        title: ''
      }).then(response => {
        router.push('/')
      })
      return Promise.reject(response)
    }
  })

export default axios
