import apiLogin from '@/api/login'
import router from '@/router'
import { setToken, parserToken, removeToken } from '@/utils/auth'

/**
  @desc members 
  @params
    jwt        jwt token
    user        user 
*/
const state = {
  jwt: '',
  user: {}
}

/**
  @desc members 
  @params
    SET_JWT         jwt token
    SET_USER        user 
*/
const mutations = {
  SET_JWT: (state, jwt) => {
    state.jwt = jwt
  },
  SET_USER: (state, user) => {
    state.user = user
  }
}

/**
  @desc
     api  data  server db  data
     jwt
     jwt  localStorage or cookie
     auth.js  decode  jwt
    decode jwt  server  data  state 

  @method
    userLogin       User 
    getuser          user 
      1. jwt user 
      2.reflash  jwt user 
    logout          
*/
const actions = {
  userLogin ({ commit, dispatch, store }, loginUser) {
    return new Promise((resolve, reject) => {
      apiLogin.userLogin(loginUser)
        .then(res => {
          if (!res.token.length) {
            throw new Error('not set jwt ')
          }
          commit('SET_JWT', res.token)
          setToken(res.token)

          //  getuser action  user data
          dispatch('getuser', state.jwt)
          resolve(res)
        })
        .catch(err => {
          reject(err)
        })
    })
  },

  getuser ({ commit, state }, token) {
    //  state  jwt
    const reToken = state.jwt
    if (!reToken) {
      commit('SET_JWT', token)
    }
    const user = parserToken(token)
    commit('SET_USER', user)
  },

  logout ({ commit, state, getters }) {
    removeToken()
    commit('SET_JWT', '')
    commit('SET_USER', {})
    // window.location.reload() // safari 
    router.go(0)
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
